import staticRoutes from 'static-routes';

/*
 * Debug Util
 */

const DEBUG_ALWAYS_ON = false;
const DEBUG = DEBUG_ALWAYS_ON || process.env.NODE_ENV === 'development';

// eslint-disable-next-line no-console
const debug = (method, ...args) => DEBUG && console[method] && console[method](...args);


/*
 * Global Config
 */

const { assets } = global.serviceWorkerOption;
const CACHE_NAME = (new Date()).toISOString();


/**
 * Helper Functions
 */

const supportsWebp = async () => {
  if (!self.createImageBitmap) return false;

  const webpData = 'data:image/webp;base64,UklGRh4AAABXRUJQVlA4TBEAAAAvAAAAAAfQ//73v/+BiOh/AAA=';
  const blob = await fetch(webpData).then(r => r.blob());
  return self.createImageBitmap(blob).then(() => true, () => false);
};

const rewriteStaticRoutes = route => (route.endsWith('/') ? route : `${route}/index.html`);

const onInstall = async () => {
  try {
    const cache = await global.caches.open(CACHE_NAME);

    const modifiedStaticRoutes = staticRoutes.map(rewriteStaticRoutes);
    let assetsToCache = [...assets, ...modifiedStaticRoutes];

    if (await supportsWebp()) {
      debug('log', 'Install Event: Webp support detected');
      assetsToCache = assetsToCache; // eslint-disable-line no-self-assign
    }

    assetsToCache = assetsToCache.map(path => new URL(path, global.location).toString());

    await cache.addAll(assetsToCache);
    debug('log', 'Install Event: Cached webpack assets', assetsToCache);

    self.skipWaiting();
  } catch (err) {
    debug('error', 'Install Event: Cache error', err);
  }
};

const onActivate = async () => {
  try {
    const cacheNames = await global.caches.keys();
    const cachesToDelete = cacheNames
      .filter(cacheName => cacheName.indexOf(CACHE_NAME) !== 0)
      .map(cacheName => global.caches.delete(cacheName));

    await Promise.all(cachesToDelete);
    debug('log', 'Activate Event: Done clearing old caches');

    self.clients.claim();
    debug('log', 'Activate Event: Claimed clients');
  } catch (err) {
    debug('error', 'Activate Event: Cache delete error', err);
  }
};

const fetchFromCache = async (request) => {
  try {
    const cacheRes = await global.caches.match(request);

    if (cacheRes) {
      debug('log', `fetchFromCache: Fetched ${request.url} from cache`);
      return cacheRes;
    }

    debug('error', `fetchFromCache: ${request.url} cache fetch error`);
    return null;
  } catch (err) {
    debug('error', 'fetchFromCache: ', err);
    return Promise.reject(err);
  }
};

const fetchFromNetwork = async (request) => {
  try {
    const networkRes = await fetch(request);

    // Bad fetch response
    if (!networkRes) {
      debug('log', `fetchFromNetwork: Null response from ${request.url}`);
      return networkRes;
    } else if (!networkRes.ok) {
      debug('log', `fetchFromNetwork: Bad response from ${request.url} (${networkRes.status} ${networkRes.type})`);
      return networkRes;
    }

    // Good fetch response
    debug('log', `fetchFromNetwork: ${request.url} fetched`);

    // Clone and store response in cache
    const cloneRes = networkRes.clone();
    global.caches
      .open(CACHE_NAME)
      .then(cache => cache.put(request, cloneRes))
      .then(() => debug('log', `fetchFromNetwork: ${request.url} cached`));

    return networkRes;
  } catch (err) {
    debug('error', 'fetchFromNetwork: ', err);
    return Promise.reject(err);
  }
};

const networkThenCache = async (request) => {
  try {
    const networkRes = await fetchFromNetwork(request);

    if (networkRes) {
      return networkRes;
    }

    const cacheRes = await fetchFromCache(request);

    if (cacheRes) {
      return cacheRes;
    }

    return null;
  } catch (err) {
    debug('error', 'Fetch Event: ', err);
    return Promise.reject(err);
  }
};

const cacheThenNetwork = async (request) => {
  try {
    const cacheRes = await fetchFromCache(request);

    if (cacheRes) {
      return cacheRes;
    }

    const networkRes = await fetchFromNetwork(request);

    if (networkRes) {
      return networkRes;
    }

    return null;
  } catch (err) {
    debug('error', 'Fetch Event: ', err);
    return Promise.reject(err);
  }
};


/**
 * Add Event Listeners
 */

self.addEventListener('install', (event) => {
  debug('log', 'Install Event');

  // Populate new cache with application assets
  event.waitUntil(onInstall());
});

self.addEventListener('activate', (event) => {
  debug('log', 'Activate Event');

  // Delete old caches
  event.waitUntil(onActivate());
});

self.addEventListener('fetch', (event) => {
  const request = event.request;

  if (request.method !== 'GET') {
    debug('log', `Fetch: Ignored non-GET (${request.method}) request`);
    return;
  }

  const url = new URL(request.url);

  if (url.origin !== location.origin) {
    debug('log', `Fetch: Ignore cross-origin request ${url.origin}`);
    return;
  }

  const urlIsStaticRoute = staticRoutes.reduce((acc, route) => acc || url.href === `${url.origin}${route}`, false);

  if (urlIsStaticRoute) {
    event.respondWith(networkThenCache(new Request(rewriteStaticRoutes(request.url))));
  } else {
    event.respondWith(cacheThenNetwork(request));
  }
});



